.person {
  text-align: center;

  &__photo {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    transition: 0.3s;

    &:hover {
      -webkit-box-shadow: 0 4px 11px 12px rgba(255, 255, 255, 0.63);
      -moz-box-shadow: 0 4px 11px 12px rgba(255, 255, 255, 0.63);
      box-shadow: 0 4px 11px 12px rgba(255, 255, 255, 0.63);
    }
  }

  &__name {
    margin-top: 12px;
    font-weight: bold;
  }

  &__position {
    margin-top: 0;
    color: #696973;
    font-style: italic;
  }

  &__text {
    position: relative;

    &::after {
      position: absolute;
      display: block;
      content: "";
      width: 40px;
      height: 33px;
      background-image: url("../images/quotes.svg");
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center;
      right: calc(100% + 6px);
      bottom: calc(100% + 6px);
    }
  }
}
